.section-content{
  background-color: $light-grey;
  > .layout-container{
    background-color: #fff;
    padding-top: ms(5);
    padding-bottom: ms(4);
      display: flex;
      flex-direction: column;
    @include breakpoint($weird-screen) {
      flex-direction: row;
    }
  }
}

.page-404 .section-content {
  > .layout-container {
    min-height: 60vh;
  }
}

body:not(.user-logged-in) .region--content .header-wrapper {
  margin-top: - ms(5);
  position: relative;
}


#main.column{
  order: 2;
  flex-grow: 1;
  flex-shrink: 0;
  @include breakpoint($weird-screen) {
    flex: 1;
  }


  & > .layout-content{
    @include breakpoint($weird-screen) {
     flex: 1 1 0px;
    }
  }
}

aside.layout-sidebar-first {
  order: 1;
}

aside.layout-sidebar-second {
  order: 3;
}

aside.layout-sidebar-first {
  @include breakpoint($medium-screen) {
    max-width: 304px;
    width: 100%;
    margin-right: 1em;
  }
}

.layout-sidebar-second{

  border-top: 8px solid $light-grey;
  margin-top: ms(3);
  padding-top: ms(0);

  @include breakpoint($narrow-screen) {
    margin-top: ms(6);
    padding-top: ms(3);
  }

  @include breakpoint($weird-screen) {
    border-top: none;
    flex: 0 0 ms(11);
    margin-top: 0;
    padding-top: 0;
  }

  @include breakpoint($medium-screen) {
    flex: 0 0 ms(12);
  }

  @include breakpoint($standard-screen) {
    flex: 0 0 ms(13);
  }

  @include breakpoint($wide-screen) {
    flex: 0 0 ms(14);
  }
}


//50 50 regions

.section-50-50 {
  &:after {
    content: '';
    clear: both;
    display: block;
  }
  @include breakpoint($narrow-screen) {
    display: flex;
    margin-left: - ms();
    margin-right: - ms();

    .layout-container {
      padding: 0;
    }
  }

  & > .region {
    margin-bottom: ms();
    @include breakpoint($narrow-screen) {
      flex-grow: 1;
      padding: ms();
    }
  }
}


//annoucement region

.region--announcements {
  background-color: $light-grey;
  & > .layout-container {
    background-color: #fff;
  }
}









